html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  font-family: "Poppins", "Arial", sans-serif;
  margin: 0 auto;
  padding: 0; }

a {
  text-decoration: none;
  color: inherit; }

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0; }

.header {
  outline: 3px dotted #bb54e4;
  position: relative; }
  .header::before {
    position: absolute;
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background-color: #f6f5f4;
    top: 115px; }

.header__header-content {
  display: flex;
  justify-content: space-between;
  min-height: 115px;
  padding: 10px 0 0 0; }
  @media (max-width: 1023px) {
    .header__header-content {
      min-height: 125px;
      padding: 0; } }
  @media (max-width: 1023px) {
    .header__header-content {
      min-height: 90px; } }

.header__burger {
  display: none;
  width: 25px;
  height: 18px;
  padding: 0;
  background: transparent;
  border: none;
  outline: none; }
  .header__burger:hover, .header__burger:focus {
    opacity: 0.7;
    cursor: pointer; }
  @media (max-width: 1023px) {
    .header__burger {
      display: flex;
      align-self: center; } }

@media (max-width: 1023px) {
  .header__burger--js svg {
    display: block;
    width: 25px;
    height: 18px;
    stroke: #f0d74b; } }

.header__search {
  position: relative;
  display: flex;
  align-items: center;
  margin: -20px auto 0 0;
  align-self: center;
  width: 100%; }
  @media (max-width: 1023px) {
    .header__search {
      display: none; } }

.header__search label {
  position: absolute;
  left: 0; }

.header__search input {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  display: flex;
  padding: 12px 10px 12px 28px;
  border: none; }
  .header__search input:hover, .header__search input:focus {
    opacity: 0.7;
    cursor: pointer; }

.header__search input::placeholder {
  color: #9b9997; }

.header__logo {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center; }
  .header__logo:hover, .header__logo:focus {
    opacity: 0.7;
    cursor: pointer; }

.header__user-bar {
  width: 100%; }
  @media (max-width: 1023px) {
    .header__user-bar {
      width: auto; } }

.footer {
  outline: 3px dotted #e45484; }

.main {
  outline: 3px dotted #1e6d48;
  flex-grow: 1; }

.centering-container {
  min-width: 320px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px; }
  @media (max-width: 1023px) {
    .centering-container {
      padding: 0 45px; } }
  @media (max-width: 727px) {
    .centering-container {
      padding: 0 15px; } }

.user-bar {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #1d1613;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 0 18px 0; }
  @media (max-width: 1023px) {
    .user-bar {
      padding: 0; } }

@media (max-width: 1023px) {
  .user-bar__login-link {
    display: none; } }

.user-bar__cart-link {
  display: flex;
  align-items: center;
  margin: 0 0 0 40px;
  align-self: center; }
  @media (max-width: 1023px) {
    .user-bar__cart-link {
      margin: 0; } }

@media (max-width: 1023px) {
  .user-bar__cart-link div {
    display: none; } }

.user-bar__cart-link svg {
  display: none; }
  @media (max-width: 1023px) {
    .user-bar__cart-link svg {
      display: flex; } }

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #1d1613;
  display: flex;
  flex-wrap: wrap;
  min-height: 60px;
  align-items: center;
  max-width: 690px;
  margin: 0 auto;
  padding: 0 0 0 105px; }
  @media (max-width: 1023px) {
    .navigation__list {
      display: none; } }

.navigation__list-item {
  margin: 0 35px 0 0; }

.navigation__expanded-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
  font-weight: 500;
  font-size: 18px;
  line-height: 23px;
  color: rgba(255, 255, 255, 0.75); }
