html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  font-family: "Poppins", "Arial", sans-serif;
  // color: #ffffff;
  margin: 0 auto;
  padding: 0;
